export default {
	data() {
		return {
			globalUser: {},
			sex: '-1'
		}
	},
	onLoad() {
		let me = this;
		let globalUser = me.getGlobalUser("globalUser");
		me.globalUser = globalUser;
		me.sex = globalUser.sex;

	},
	methods: {
		sexChange(e) {
			this.sex = e.detail.value;
		},
		formSubmitSex() {
			let me = this;
			let sex = me.sex;

			uni.request({
				url: me.serverUrl + "/user/modifyUserinfo?qq=309063773",
				data: {
					"userId": me.globalUser.id,
					"sex": sex
				},
				header: {
					"headerUserId": me.globalUser.id,
					"headerUserToken": me.globalUser.userUniqueToken
				},
				method: "POST",
				success(res) {
					let resData = res.data;
					// console.log(typeof(resData));
					if (resData.status == 200) {
						// 获得最新的用户数据
						let userInfo = resData.data;
						uni.setStorageSync("globalUser", userInfo);
						uni.navigateBack({
							delta: 1
						})
					} else if (resData.status == 502 || resData.status == 500) {
						uni.showToast({
							title: res.data.msg,
							image: "../../static/icos/error.png",
							duration: 2000
						})
					}
				}
			})

		}
	}
}
